/**************************************
 Radio styles
**************************************/
ul.check-box {
  list-style: none;
  padding-left: 0; }

.label--radio {
  position: relative;
  margin: 5px;
  line-height: 135%;
  cursor: pointer; }

.radio {
  position: relative;
  margin: 0 10px 0 0;
  cursor: pointer; }

.radio:before {
  transition: -webkit-transform 0.4s cubic-bezier(0.45, 1.8, 0.5, 0.75);
  transition: transform 0.4s cubic-bezier(0.45, 1.8, 0.5, 0.75);
  transition: transform 0.4s cubic-bezier(0.45, 1.8, 0.5, 0.75), -webkit-transform 0.4s cubic-bezier(0.45, 1.8, 0.5, 0.75);
  -webkit-transform: scale(0, 0);
  transform: scale(0, 0);
  content: "";
  position: absolute;
  top: 0px;
  left: 2px;
  z-index: 1;
  width: 12px;
  height: 12px;
  background: #11AAC1;
  border-radius: 50%; }

.radio:checked:before {
  -webkit-transform: scale(1, 1);
  transform: scale(1, 1); }

.radio:after {
  content: "";
  position: absolute;
  top: -4px;
  left: -2px;
  width: 18px;
  height: 18px;
  background: #FBFBFB;
  border: 1px solid #D9D9D9;
  border-radius: 50%; }

.label--radio + input[type="radio"]:disabled:after {
  background-color: #e7e7e7;
  border-color: #e7e7e7; }

input[type="radio"]:disabled + label {
  opacity: 0.55; }

.radio[checked="checked"]:disabled:before {
  background: #D9D9D9; }
